        <!-- view budget modal ends -->

        <!-- Edit budget factor modal starts -->
 <div class="modal fade mymodal" id="edit-budget-modal" data-bs-backdrop="false" data-bs-keyboard="false" tabindex="-1" aria-labelledby="" aria-hidden="true">
  <div class="modal-dialog modal-xl">
    <div class="modal-content">
      <div class="modal-header">
        <h1 class="modal-title fs-5" id="edit-budget-modal">Account Budget {{ account_budget.account_budget }}</h1>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="closeModal()"></button>
      </div>
      <form @submit.prevent="updateAccountBudget()">
        <div class="modal-body">
            <div class="row mb-3">
            <div class="col-md-6">
                <label for="cheque_no">Cheque NO.</label>
              <input type="text" v-model="cheque_no" class="form-control" placeholder="Enter Cheque NO." min="1" required>
            </div>
            <div class="col-md-6">
                <label for="remark">Remark</label>
              <input type="text" v-model="remark" class="form-control" placeholder="Enter Remark">
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="closeModal()" id="close_cheque">Close</button>
          <button type="submit" class="btn btn-primary" :disabled="loading">
            <span v-if="loading" class="spinner-border spinner-border-sm text-white me-2" role="status"></span>
            <span v-if="loading"></span>
            <span v-else>Save</span>
          </button>
        </div>
      </form>
    </div>
  </div>
</div>
        <!-- Edit budget factor modal ends -->
    </div>
</template>

<style>
    .table3 .actions svg {
        padding: 2px;
    }
</style>

<script setup>
    import { onMounted, ref, inject, computed } from 'vue';
    import axios from 'axios';
    import { useMeta } from '@/composables/use-meta';
    
    useMeta({ title: 'Account Budgets' });  
    const loading = ref(false);
    const showMessage = inject('showMessage');
    const showError = inject('showError');
    const formatDate = inject('formatDate');
    const tableShimmer = inject('tableShimmer');
    
    const loading_spinner = ref(false);
    const columns = ref(['SNo','account_budget','type','date','amount','cheque_no','remark','actions']);    
    const itemColumns = ref(['sno','ref_no','project','type','description','quantity','rate','amount','balance','requester','actions']);    
    const budget = ref([]);
    const account_budget = ref({}); 
    const cheque_no = ref('');
    const formatNumber = inject('formatNumber');   
    
    const table_option = ref({
        perPage: 20,
        perPageValues: [20, 50 ,100],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['SNo', 'account_budget','status'],
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });

    const itemTableOptions = ref({
        perPage: 10,
        perPageValues: [10, 20, 50],
        skin: 'table table-hover table-striped',
        sortable: ['SNo', 'description', 'quantity'],
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search items...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });

    // Computed property to extract and flatten items for the modal
    const modalItems = computed(() => {
        if (!account_budget.value.items || !Array.isArray(account_budget.value.items)) {
            return [];
        }
        
        const items = [];
        
        let runningBalance = 0;
        
        account_budget.value.items.forEach(budgetItem => {
            if (budgetItem.approval_list && budgetItem.approval_list.items) {
                budgetItem.approval_list.items.forEach(item => {
                    if (item.requisition_items) {
                        const reqItem = item.requisition_items;
                        const requisitionType = reqItem.requisition_type;
                        const requisition = requisitionType?.requisition;
                        const project = requisition?.project;
                        const itemQuantity = item.quantity;
                        
                        const itemTotal = parseFloat(itemQuantity) * parseFloat(reqItem.rate);
                        runningBalance += itemTotal;
                        
                        items.push({
                            sno: items.length + 1,
                            ref_no: budgetItem.approval_list.id,
                           type: budgetItem.approval_list?.approval_group?.name || '-',
                            description: reqItem.description || '-',
                            quantity: itemQuantity || '0',
                            rate: formatNumber(reqItem.rate || 0),
                            amount: formatNumber(itemTotal),
                            balance: formatNumber(runningBalance),
                            units: reqItem.units || '',
                            project: project?.short_name || project?.project_name || '-',
                            requester: budgetItem.approval_list.requester?.name || '-',
                            status: budgetItem.approval_list.status?.name || '-',
                            status_color: budgetItem.approval_list.status?.color || 'secondary'
                        });
                    }
                });
            }
        });
        
        return items;
    });

    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });


    const getAccountBudget = async () => {
        loading_spinner.value = true;
        try {
            const response = await axiosInstance.get('account-budgets');
            budget.value = response.data.map((item, index) => {
                // Calculate total amount from requisition items
                let totalAmount = 0;
                if (item.items && Array.isArray(item.items)) {
                    item.items.forEach(budgetItem => {
                        if (budgetItem.approval_list && budgetItem.approval_list.items) {
                            budgetItem.approval_list.items.forEach(approvalItem => {
                                if (approvalItem.requisition_items) {
                                    const reqItem = approvalItem.requisition_items;
                                    const itemTotal = parseFloat(approvalItem.quantity || 0) * parseFloat(reqItem.rate || 0);
                                    totalAmount += itemTotal;
                                }
                            });
                        }
                    });
                }

                return {
                    SNo: index + 1,
                    id: item.id,
                    account_budget: item.name,
                    type: item.type,
                    cheque_no: item.cheque_no,                
                    remark: item.remark,
                    date: formatDate(item.created_at),
                    amount: formatNumber(totalAmount), // Add the calculated total amount
                    items: item.items,                
                };
            });
        } catch (error) {
            console.error(error);
            showError(error.message);
        } finally {
            loading_spinner.value = false;
        }
    };
    
    const openViewModal = (row) => {
        account_budget.value = row;
    };
    
    const closeModal = () => {
        account_budget.value = {};
    };
    
const printBudget = async () => {
  try {
    const iframe = document.createElement("iframe");
    iframe.style.display = "none";
    document.body.appendChild(iframe);
    const budgetNo = `${account_budget.value.id}`;

    // Date formatting
    const now = new Date();
    const day = String(now.getDate()).padStart(2, '0');
    const month = String(now.getMonth() + 1).padStart(2, '0');
    const year = now.getFullYear();
    const formattedDate = `${day}/${month}/${year}`;
    const formatDate = `${day}.${month}.${year}`;
    const formattedDateTime = `${formattedDate}, ${now.toLocaleTimeString([], {hour: '2-digit', minute: '2-digit'})}`;

    // Group items by project
    const groupedByProject = {};
    
    if (modalItems.value && modalItems.value.length > 0) {
      modalItems.value.forEach(item => {
        if (!groupedByProject[item.project]) {
          groupedByProject[item.project] = [];
        }
        groupedByProject[item.project].push(item);
      });
    }

    // Calculate totals per project and overall
    const projectTotals = {};
    let overallTotal = 0;
    
    Object.keys(groupedByProject).forEach(projectName => {
      const projectTotal = groupedByProject[projectName].reduce((sum, item) => {
        const itemAmount = parseFloat(item.quantity || 0) * parseFloat(item.rate?.replace(/,/g, '') || 0);
        return sum + itemAmount;
      }, 0);
      
      projectTotals[projectName] = projectTotal;
      overallTotal += projectTotal;
    });

    // Extract approval chain from the first approval_list
    const getApprovalChain = () => {
      if (!account_budget.value.items || account_budget.value.items.length === 0) {
        return { 
          approval_roles: [], 
          approval_steps: [],
          roles_sequence: [] 
        };
      }
      
      // Get the first approval_list
      const firstApprovalList = account_budget.value.items[0]?.approval_list;
      if (!firstApprovalList) {
        return { 
          approval_roles: [], 
          approval_steps: [],
          roles_sequence: [] 
        };
      }
      
      // Extract approval roles from approval_group
      const approvalRoles = firstApprovalList.approval_group?.approval_roles || [];
      
      // Extract approval chain steps
      const approvalChain = firstApprovalList.approval_chain || [];
      
      // Create combined roles sequence with their status
      const rolesSequence = approvalRoles.map(role => {
        const chainStep = approvalChain.find(step => step.role_id === role.role_id);
        return {
          name: role.role?.name || 'N/A',
          position: role.position,
          status: chainStep?.status || { name: 'pending', id: 2 },
          status_id: chainStep?.status_id || 2,
          remark: chainStep?.remark || null,
          approval_date: chainStep?.created_at ? formatDateShort(chainStep.created_at) : null
        };
      }).sort((a, b) => a.position - b.position);
      
      return {
        approval_roles: approvalRoles,
        approval_steps: approvalChain,
        roles_sequence: rolesSequence
      };
    };
    
    const formatDateShort = (dateString) => {
      if (!dateString) return 'N/A';
      const date = new Date(dateString);
      return `${date.getDate().toString().padStart(2, '0')}/${(date.getMonth() + 1).toString().padStart(2, '0')}/${date.getFullYear()}`;
    };
    
    const voucher = getApprovalChain();

    // Function to generate table HTML for a project
    const generateProjectTable = (projectName, items) => {
      let tableHTML = `
<div class="section-title">${projectName}</div>
<table class="items" style="margin-top: 10px;">
  <thead>
    <tr>
      <th style="width: 30px;"><strong>Sno.</strong></th>
      <th><strong>Ref</strong></th>
      <th><strong>Description</strong></th>
      <th><strong>Requester</strong></th>
      <th><strong>Quantity</strong></th>
      <th><strong>Rate</strong></th>
      <th><strong>Amount</strong></th>
    </tr>
  </thead>
  <tbody>
  `;
      
      items.forEach((item, index) => {
        tableHTML += `
    <tr>
      <td class="text-center">${index + 1}</td>
      <td class="text-center">${item.ref_no}</td>
      <td class="text-start">${item.description}</td>
      <td class="text-start">${item.requester}</td>      
      <td class="text-end">${item.quantity}</td>
      <td class="text-end">${item.rate}</td>
      <td class="text-end">${item.amount}</td>
    </tr>
    `;
      });
      
      tableHTML += `
    <tr style="font-weight: bold; background-color: #f8f8f8;">
      <td colspan="6" class="text-end"><strong>Subtotal for ${projectName}:</strong></td>
      <td class="text-end"><strong>${formatNumber(projectTotals[projectName])}</strong></td>
    </tr>
  </tbody>
</table>
<div style="margin-bottom: 30px;"></div>
`;
      return tableHTML;
    };

    const printContent = `
<!DOCTYPE html>
<html>
<head>
<title>Account Budget</title>
<style>
  body { font-family: Arial, sans-serif; margin: 40px; font-size: 13px; }
  .header { display: flex; justify-content: space-between; align-items: flex-start; }
  .logo { width: 160px; height: auto; }
  .budget-box { border: 1px solid #000; padding: 4px 8px; font-size: 11px; text-align: center; margin-left: auto; border-radius: 5px; }
  .company-title { text-align: center; margin-top: 10px; }
  .reg-info { position: absolute; top: 120px; right: 40px; text-align: right; font-size: 10px; line-height: 1.4; }
  .title { text-align: center; font-weight: bold; margin: 20px 0 10px 0; }
  .section-title { text-align: left; font-weight: bold; margin: 30px 0 15px 0; font-size: 14px; text-decoration: underline; color: #333; }
  table { width: 100%; border-collapse: collapse; margin-top: 10px; }
  table.items th, table.items td { border: 1px solid #000; padding: 6px; font-weight: normal; }
  table.items thead tr th { background-color: #f8f8f8; border: 1px solid #000; font-weight: bold; }
  table.info-table { border: none; margin-top: 15px; }
  table.info-table td { border: none; padding: 5px; }
  table.signatures th, table.signatures td { border: 1px solid #000; padding: 10px; text-align: center; }
  table.signatures th { background-color: #f5f5f5; font-weight: bold; }
  .text-right { text-align: right; }
  .text-end { text-align: end; }
  .text-center { text-align: center; }
  .text-start { text-align: start; }
  .approval-status { font-size: 11px; padding: 2px 6px; border-radius: 3px; }
  .status-approved { background-color: #d4edda; color: #155724; }
  .status-pending { background-color: #fff3cd; color: #856404; }
  .status-rejected { background-color: #f8d7da; color: #721c24; }
  .project-summary { margin-top: 40px; padding: 15px; border: 1px solid #ccc; background-color: #f9f9f9; }
  .overall-total { margin-top: 20px; padding: 10px; background-color: #e8f4fd; border: 2px solid #000; font-size: 14px; }
  @media print { 
    body { margin: 10mm; } 
    .no-print { display: none; } 
    table { page-break-inside: avoid; }
    .signatures-table { page-break-before: always; }
  }
</style>
</head>
<body>
<div class="header">
  <img src="https://bushman.ink/copex.jpg" class="logo" />
  <div class="budget-box">
    BUDGET NO. <br>
    <span style="color:red;">00${budgetNo}</span>
  </div>
</div>

<div class="reg-info">
  TIN: 103-826-519<br>
  VRN: 20-01-017530R
</div>

<div class="company-title">
  <h2>COPEX CONTRACTORS CO.LTD</h2>
  <div>Msamvu, P.O.BOX 678, Morogoro</div>
  <div>Mobile, 0713 000 777, 0657 111 555</div>
</div>

<div class="title">${account_budget.value.account_budget || 'Account Budget'}</div>

<table class="info-table">
  <tr>
    <td style="width: 33%;"><strong>Budget Type:</strong> ${account_budget.value.type || '-'}</td>
    <td style="width: 33%; text-align: center;"><strong>Date:</strong> ${formatDate}</td>
    <td style="width: 33%; text-align: right;">${account_budget.value.cheque_no ? `<strong>Cheque No:</strong> ${account_budget.value.cheque_no}` : ''}</td>
  </tr>
</table>


${Object.keys(groupedByProject).length > 0 ? 
  Object.keys(groupedByProject).map(projectName => 
    generateProjectTable(projectName, groupedByProject[projectName])
  ).join('')
  : '<p style="text-align: center; margin-top: 20px;">No items found for this budget.</p>'
}

<div class="text-end">
  <strong style = "text-start">GRAND TOTAL:</strong> <span style="font-size: 16px; color: #d9534f;">${formatNumber(overallTotal)}</span>
</div>

<div class="section-title" style="margin-top: 40px; text-align: center; text-decoration: none;">
  APPROVAL CHAIN
</div>

${voucher.roles_sequence && voucher.roles_sequence.length > 0 ? `
<table class="signatures" style="margin-top: 15px;">
  <thead>
    <tr>
    ${voucher.roles_sequence.map(role => `
      <th class="text-center">${role.name}</th>
    `).join('')}    
    </tr>    
  </thead>
  <tbody>
    <tr>
    ${voucher.roles_sequence.map(role => `
      <td class="text-center">
        <span class="approval-status ${role.status_id === 3 ? 'status-approved' : role.status_id === 4 ? 'status-rejected' : 'status-pending'}">
          ${role.status.name ? role.status.name.toUpperCase() : 'PENDING'}
        </span>
      </td>
      `).join('')}
    </tr>
  </tbody>
</table>
` : '<p style="text-align: center; color: #666;">No approval chain information available.</p>'}

