`;

    const iframeDoc = iframe.contentWindow.document;
    iframeDoc.open();
    iframeDoc.write(printContent);
    iframeDoc.close();

    iframe.onload = () => {
      iframe.contentWindow.focus();
      iframe.contentWindow.print();
      setTimeout(() => {
        document.body.removeChild(iframe);
      }, 1000);
    };
  } catch (error) {
    showError(`Failed to print budget: ${error.message}`);
  }
};
 const openEditModal = (row) => {
    account_budget.value = row;
    cheque_no.value = row.cheque_no;
    remark.value = row.remark;
};
const remark = ref('')
  const updateAccountBudget = async () => {
    loading.value = true;
    const payload = {
        cheque_no: cheque_no.value, 
        remark: remark.value,        
    };
    try {
      const response = await axiosInstance.put(`account-budgets/${account_budget.value.id}`, payload);
      if (response.status === 200) {
        document.getElementById('close_cheque').click();
        getReactiveAccountBudget();
        showMessage('Account Budget updated successfully!');
        closeModal();
      }
    } catch (error) {
      showError(error.response?.data?.message || error.message || 'Failed to update account budget');
    } finally {
      loading.value = false;
    }
  };

  const getReactiveAccountBudget = async () => {
       const response = await axiosInstance.get('account-budgets');
            budget.value = response.data.map((item, index) => {
                // Calculate total amount from requisition items
                let totalAmount = 0;
                if (item.items && Array.isArray(item.items)) {
                    item.items.forEach(budgetItem => {
                        if (budgetItem.approval_list && budgetItem.approval_list.items) {
                            budgetItem.approval_list.items.forEach(approvalItem => {
                                if (approvalItem.requisition_items) {
                                    const reqItem = approvalItem.requisition_items;
                                    const itemTotal = parseFloat(approvalItem.quantity || 0) * parseFloat(reqItem.rate || 0);
                                    totalAmount += itemTotal;
                                }
                            });
                        }
                    });
                }

                return {
                    SNo: index + 1,
                    id: item.id,
                    account_budget: item.name,
                    type: item.type,
                    cheque_no: item.cheque_no,                
                    remark: item.remark,
                    date: formatDate(item.created_at),
                    amount: formatNumber(totalAmount), // Add the calculated total amount
                    items: item.items,                
                };
            });
  }

  
    onMounted(() => {
        getAccountBudget();
    });
</script>
